*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.headerInfo {
    padding-left: 15px;
    margin-right: auto;
    font-size: 30px;
}

img, input[type="image"] {
    width: 150px;
    padding-right: 15px;
}

input[type="file"] {
    display: none;
}

header {
    padding: 15px;
    background-color: lightblue;
    /* border: 1px solid red; */
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: center;
    width: 100%;
    max-height: 300px;
}

#NameInput {
    font-size: 1.5em;
    max-width: 500px;
}

.submitBtn {
    padding: 10px;
    margin: 2px;
    margin-left: 10px;
    cursor: pointer;
}

#profilePic {
    cursor: pointer;
}
